import request from '../utils/request.js'

//carinfo
export function queryByCondition(data){
    return request({
        url: '/communicationinfo/queryByCondition',//
        method: 'post',//
        data
    })
}
//
export function add(data){
    return request({
        url: '/communicationinfo/add',//
        method: 'post',//
        data
    })
}
//
export function update(data){
    return request({
        url: '/communicationinfo/update',//
        method: 'post',//
        data
    })
}
export function deleteById(data){
    return request({
        url: '/communicationinfo/deleteById',//
        method: 'post',//
        data
    })
}