import request from '../utils/request.js'

//carinfo
export function queryByCondition(data){
    return request({
        url: '/merchandiseinfo/queryByCondition',//
        method: 'post',//
        data
    })
}
//
export function add(data){
    return request({
        url: '/merchandiseinfo/add',//
        method: 'post',//
        data
    })
}
//
export function update(data){
    return request({
        url: '/merchandiseinfo/update',//
        method: 'post',//
        data
    })
}
export function deleteById(data){
    return request({
        url: '/merchandiseinfo/deleteById',//
        method: 'post',//
        data
    })
}