import request from '../utils/request.js'

	
export function queryByCondition(data){
	return request({
		url: '/addressinfo/queryByCondition',//
		method: 'post',//
		data
	})
}

export function addAddress(data) {
	return request({
		url: '/addressinfo/add',
		method: 'post',
		data
	})
}
export function update(data){
	return request({
		url: '/addressinfo/update',//
		method: 'post',//
		data
	})
}

export function deleteAddress(data){
	return request({
		url: '/addressinfo/deleteById',//
		method: 'post',//
		data
	})
}