import request from '../utils/request.js'


export function queryByCondition(data){
    return request({
        url: '/transportationinfo/queryByCondition',//
        method: 'post',//
        data
    })
}

export function addTransportation(data) {
    return request({
        url: '/transportationinfo/add',
        method: 'post',
        data
    })
}
export function updateTransportation(data){
    return request({
        url: '/transportationinfo/update',//
        method: 'post',//
        data
    })
}

export function deleteTransportation(data){
    return request({
        url: '/transportationinfo/deleteById',//
        method: 'post',//
        data
    })
}