import request from '../utils/request.js'

	
export function queryByName(data){
	return request({
		url: '/fixinfo/getByRepairmanName',//
		method: 'post',//
		data
	})
}
export function update(data){
	return request({
		url: '/fixinfo/updateFixLog',//
		method: 'post',//
		data
	})
}
export function insert(data){
	return request({
		url: '/fixinfo/insertFixLog',//
		method: 'post',//
		data
	})
}
export function deleteFixLog(data){
	return request({
		url: '/fixinfo/deleteFixLog',//
		method: 'post',//
		data
	})
}