import request from '../utils/request.js'

	
export function selectAll(){
	return request({
		url: '/certificateinfo/selectAll',//
		method: 'get',//
	})
}

//selectByName, name, 
export function selectByName(data){
	return request({
		url: '/certificateinfo/selectOne',//
		method: 'post',//
		data
	})
}
export function edit(data){
	return request({
		url: '/certificateinfo/edit',//
		method: 'post',//
		data
	})
}

//addByNametemp(formData), , 
export function addByName(data){
	return request({
		url: '/certificateinfo/add',//
		method: 'post',//
		data
	})
}
export function deleteById(data){
	return request({
		url: '/certificateinfo/deleteById',//
		method: 'post',//
		data
	})
}