import request from '../utils/request.js'

	
export function queryByName(data){
	return request({
		url: '/companyinfo/queryByName',//
		method: 'post',//
		data
	})
}
export function update(data){
	return request({
		url: '/companyinfo/updateCompany',//
		method: 'post',//
		data
	})
}
export function insert(data){
	return request({
		url: '/companyinfo/insertCompany',//
		method: 'post',//
		data
	})
}
export function deleteCompany(data){
	return request({
		url: '/companyinfo/deleteCompany',//
		method: 'post',//
		data
	})
}