<template>
  <div>
    <el-input
        style="width: 190px;margin-right: 40px;margin-top: 20px;margin-bottom: 20px;"
        v-model="listQuery.employeeName"
        size="small"
        placeholder=""
        clearable
        class="filter-item"
    />
    <el-button-group style="margin-right: 20px;margin-top: 20px;margin-bottom: 20px;"
                     class="filter-item">
      <el-button
          :loading="searchloading"
          size="small"
          type="primary"
          icon="el-icon-search"
          @click="search"
      >
        
      </el-button>
      <el-button
          size="small"
          type="primary"
          icon="el-icon-refresh"
          @click="refresh"
      >
        
      </el-button>
      <el-button
          size="small"
          type="primary"
          icon="el-icon-plus"
          @click="insert"
      >
        
      </el-button>
    </el-button-group>

    <el-table
        v-loading="listLoading"
        :data="employeeList"
        element-loading-text=""
        border
        fit
        height="500px"
        class="table-container"
        highlight-current-row
    >
      <el-table-column
          label=""
          width="100"
          align="center"
      >
        <template slot-scope="scope">
          {{ scope.row.index }}
        </template>
      </el-table-column>
      <el-table-column
          label=""
          width="155"
          align="center"
      >
        <template slot-scope="scope">
          {{ scope.row.companyName }}
        </template>
      </el-table-column>
      <el-table-column
          label=""
          width="100"
          align="center"
      >
        <template slot-scope="scope">
          {{ scope.row.employeeName }}
        </template>
      </el-table-column>
      <el-table-column
          label=""
          width="80"
          align="center"
      >
        <template slot-scope="scope">
          <span>{{ scope.row.employeeGender }}</span>
        </template>
      </el-table-column>
      <el-table-column
          label=""
          width="165"
          align="center"
      >
        <template slot-scope="scope">
          {{ scope.row.employeeAddress }}
        </template>
      </el-table-column>
      <el-table-column
          label=""
          width="160"
          align="center"
      >
        <template slot-scope="scope">
          {{ scope.row.employeeMobile }}
        </template>
      </el-table-column>
      <el-table-column
          label=""
          width="100"
          align="center"
      >
        <template slot-scope="scope">
          {{ scope.row.employeeStatus }}
        </template>
      </el-table-column>
      <el-table-column
          label=""
          width="200"
          align="center"
      >
        <template slot-scope="scope">
          {{ scope.row.employeeNumber }}
        </template>
      </el-table-column>
      <el-table-column
          label=""
          width="200"
          align="left"
      >
        <template slot-scope="scope">
          <el-button-group>
            <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="edit(scope)"
            >
              
            </el-button>
            <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="del(scope)"
            >
              
            </el-button>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
    <pagination
        v-show="listQuery.total > 0"
        :total="listQuery.total"
        :page.sync="listQuery.page"
        :limit.sync="listQuery.limit"
        @pagination="initEmployeeList"
    />
    <el-dialog
        :visible.sync="dialogVisible"
        :title="dialogType === 'modify' ? '' : ''"
    >
      <el-form
          ref="dataForm"
          :model="temp"
          label-width="150px"
          label-position="right"
      >
        <el-form-item label="">
<!--          <el-input v-model="temp.companyId" placeholder="" />-->
          <el-select v-model="temp.companyId" placeholder="" size="small">
            <el-option
                v-for="item in companyList"
                :key="item.value"
                :label="item.companyId"
                :value="item.companyId">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="temp.employeeName" placeholder="" />
        </el-form-item>
        <el-form-item label="">
<!--          <el-input v-model="temp.employeeGender" placeholder="" />-->
          <el-select v-model="temp.employeeGender" placeholder="" size="small">
            <el-option
                v-for="item in gender"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="temp.employeeAddress" placeholder="" />
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="temp.employeeMobile" placeholder="" />
        </el-form-item>
        <el-form-item label="">
<!--          <el-input v-model="temp.employeeStatus" placeholder="" />-->
          <el-radio-group v-model="temp.employeeStatus">
            <el-radio label="" value=""></el-radio>
            <el-radio label="" value =""></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="temp.employeeNumber" placeholder="" />
        </el-form-item>
      </el-form>
      <el-button type="danger" @click="dialogVisible = false">
        
      </el-button>
      <el-button type="primary" @click="submit">
        
      </el-button>
    </el-dialog>
  </div>
</template>

<script>
import { queryByPage,add,update,deleteById} from '@/api/getEmployeeinfo.js';
import { deepClone } from '@/utils/index.js';
import Pagination from '@/components/Pagination'
import { setStorage, getStorage} from "@/utils/localStorage.js";
import {Message} from "element-ui";
import {queryByName} from "@/api/getCompanyinfo";
const _temp = {
  companyId: "",
  employeeAddress: "",
  employeeDate: "",
  employeeGender: "",
  employeeId: "",
  employeeMobile: "",
  employeeName: "",
  employeeNumber: "",
  employeeStatus: "",
}

export default {
  components: {
    Pagination
  },
  data() {
    return {
      gender:[{
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }],
      searchloading:false, //
      listLoading:true,//
      employeeList:[],       //
      companyList:[],
      listQuery:{       // 
        limit: 10,//
        page: 1,//
        total: 0,//
        employeeName: "" //
      },
      temp: Object.assign({}, _temp),
      dialogVisible: false,   //
      dialogType: 'create',
    }
  },

  methods: {
    getCompanyId() {
      queryByName({}).then((res) => {
        if (res != -1) {
          // console.log("res\n");
          // console.log(res);
          res.datas.forEach((item, index) => {
            item.index = index + 1;
            //console.log(item)
          })
          this.companyList = res.datas;
        }

      })
    },
    //
    initEmployeeList(){
      this.listLoading = true;
      let data = {
        employeeName: this.listQuery.employeeName,
      }
      // console.log("data\n")
      // console.log(data)
      queryByPage(data).then((res)=>{
        // console.log("res!!")
        // console.log(res)
        if(res != -1){
          //
          if(res.datas.length===0){  //
            this.listLoading = false;
            Message({
              message: "",
              type: "error"
            })
          }
          res.datas.forEach((item, index) => {
            //index
            // item.index= (this.listQuery.page-1) * this.listQuery.limit+index+1;
            item.index = index + 1;
          })
          // console.log("hhhhhhhhh")
          // console.log(this.listQuery)
          // console.log(res.datas)
          // console.log("*********")
          this.employeeList = res.datas.slice((this.listQuery.page-1) * this.listQuery.limit,this.listQuery.page * this.listQuery.limit);
          this.listQuery.total = res.total;
          setTimeout(() => {
            this.listLoading = false;
          }, 100)
        }

      })
    },
    //
    search(){
      this.searchloading = true
      this.initEmployeeList()
      setTimeout(() => {
        this.searchloading = false;
      }, 200)
    },
    //
    refresh() {
      this.listQuery.page=1;
      this.initEmployeeList()
    },
    resetTemp() {
      this.temp = Object.assign({}, _temp)
    },
    insert() {
      this.getCompanyId();
      this.resetTemp()
      this.dialogVisible = true
      this.dialogType = 'create'
      this.$nextTick(() => {
        this.$refs['dataForm'].clearValidate()
      })
    },
    edit(scope) {
      this.getCompanyId();
      this.resetTemp()
      this.dialogVisible = true
      this.dialogType = 'modify'
      // console.log("this item is !!:\n")
      // console.log(scope.row)
      this.temp = deepClone(scope.row)
      this.$nextTick(() => {
        this.$refs['dataForm'].clearValidate()
      })
    },
    del(scope) {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        deleteById({id:scope.row.employeeId}).then(()=>{
          this.initEmployeeList()
          setTimeout(() => {
            this.list.splice(scope.$index, 1)  //
            this.$message({
              message: '',
              type: 'success'
            })
          }, 100)
        })

      })
    },
    submit() {
      if (this.listLoading) {
        return
      }
      this.listLoading = true
      //
      if(this.dialogType==="create"){
        // console.log("")
        // console.log( this.temp)
        add(this.temp)
      }else  if(this.dialogType==="modify"){
        // console.log("")
        // console.log( this.temp)

        update(this.temp);

      }
      setTimeout(() => {
        this.$message({
          message: '',
          type: 'success'
        })
        this.dialogVisible = false
        this.listLoading = false
        this.initEmployeeList()
      }, 100)
    }
  },

  mounted() {
    this.$nextTick(() => {
      this.initEmployeeList();
    })
  },

}
</script>

<style>
.w-200{
  width: 200px;
}
.filter-item{
  display: inline-flex;
  vertical-align: middle;
  margin-bottom: 10px;
}
</style>