import request from '../utils/request.js'

	
export function selectAll(){
	return request({
		url: '/matetialinfo/selectAll',//
		method: 'post',//
	})
}
export function selectByName(data){
	return request({
		url: '/matetialinfo/selectByName',//
		method: 'post',//
		data
	})
}
export function edit(data){
	return request({
		url: '/matetialinfo/edit',//
		method: 'post',//
		data
	})
}
export function addByName(data){
	return request({
		url: '/matetialinfo/add',//
		method: 'post',//
		data
	})
}
export function deleteById(data){
	return request({
		url: '/matetialinfo/deleteById',//
		method: 'post',//
		data
	})
}

export function getMaterialAndTypeList(data){
	return request({
		url: '/matetialinfo/getMaterialAndTypeList',//
		method: 'post',//
		data
	})
}

export function selectType(){
	return request({
		url: '/matetialinfo/selectType',//
		method: 'post',//
		
	})
}