import request from '../utils/request.js'

//employeeinfo
export function queryByPage(data){
    return request({
        url: '/employeeinfo/queryByPage',//
        method: 'post',//
        data
    })
}
//
export function add(data){
    return request({
        url: '/employeeinfo/add',//
        method: 'post',//
        data
    })
}
//
export function update(data){
    return request({
        url: '/employeeinfo/update',//
        method: 'post',//
        data
    })
}
export function deleteById(data){
    return request({
        url: '/employeeinfo/deleteById',//
        method: 'post',//
        data
    })
}