import request from '../utils/request.js'

//machineinfo
export function queryByPage(data){
    return request({
        url: '/machineinfo/queryByPage',//
        method: 'post',//
        data
    })
}

//
export function add(data){
    return request({
        url: '/machineinfo/add',//
        method: 'post',//
        data
    })
}
//
export function update(data){
    return request({
        url: '/machineinfo/update',//
        method: 'post',//
        data
    })
}
export function deleteById(data){
    return request({
        url: '/machineinfo/deleteById',//
        method: 'post',//
        data
    })
}