<template>
	<!-- <el-container style="height: 100%; border: 1px solid #000000"> -->
	<el-container style="height: 100%;">
		<el-header style="font-size: 12px;">
			<span class="el-menu-item menu-text"
				style="font-size: 25px;font-weight: bolder;color:white;width:250px;"></span>
			<!-- 			    <el-menu-item v-for="(item, index) in menuList" :key="index" style="width: 128px;" :index="item.path" :route="{path : item.path,query: {childrenMenus: JSON.stringify(item.children)}}"><i :class="item.icon"></i> {{item.title}}</el-menu-item> -->
			<!-- <el-menu-item index="/managerPage" :route="{path :'/managerPage'}"><i class="el-icon-s-tools"></i> </el-menu-item> -->
			<el-popconfirm confirm-button-text='' cancel-button-text='' icon="el-icon-info" icon-color="red"
				title=" ? " @confirm="loginOutUser()">
				<span class="el-menu-item menu-text menu-float" slot="reference"></span>
			</el-popconfirm>

			<img class="el-menu-item menu-text menu-img"
				src="https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fp0.itc.cn%2Fq_70%2Fimages03%2F20210626%2F71f657c7c43242a684f22c3ea6b858c8.gif&refer=http%3A%2F%2Fp0.itc.cn&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=auto?sec=1658753581&t=00bc4b5c7ae62191b0ffe16863280f25" />
			<span class="el-menu-item menu-text menu-float">{{userName}}, {{authorityName}}</span>
		</el-header>

		<el-container style="height: 100%	; border: 1px solid #eee">
			<el-aside width="250px" style="background-color: rgb(237, 236, 246) ">
				<el-menu router :default-active="this.$router.path" style="height:500px">
					<el-menu-item index="mainPage"><i class="el-icon-message"></i></el-menu-item>
					<el-submenu index="1">
						<template slot="title"><i class="el-icon-message"></i></template>

						<el-menu-item index="employee"></el-menu-item>
						<el-menu-item index="car"></el-menu-item>
						<el-menu-item index="address"></el-menu-item>
						<el-menu-item index="company"></el-menu-item>
						<el-menu-item index="material"></el-menu-item>
						<el-menu-item index="certification"></el-menu-item>
						<el-menu-item index="goods"></el-menu-item>
						<el-menu-item index="build"></el-menu-item>
						<el-menu-item index="equipment"></el-menu-item>
					</el-submenu>
					<el-submenu index="2">
						<template slot="title"><i class="el-icon-menu"></i></template>
						
						<el-menu-item index="carManage"></el-menu-item>
						<el-menu-item index="goodsManage"></el-menu-item>
					</el-submenu>
					<el-submenu index="3">
						<template slot="title"><i class="el-icon-setting"></i></template>
						<el-menu-item index="carFixLog"></el-menu-item>
					</el-submenu>
          <el-submenu index="4">
            <template slot="title"><i class="el-icon-truck"></i></template>
            <el-menu-item index="carOperation"></el-menu-item>
          </el-submenu>
          <el-submenu index="5">
            <template slot="title"><i class="el-icon-view"></i></template>
            <el-menu-item index="communication"></el-menu-item>
            <el-menu-item index="advertisement"></el-menu-item>
          </el-submenu>

				</el-menu>
			</el-aside>

			<el-main>
				<div>
					<el-breadcrumb class="app-breadcrumb" separator=">">
						<transition-group name="breadcrumb">
							<el-breadcrumb-item v-for="item in levelList" :key="item.path" :to="{ path: item.path }">
								<span>{{ item.meta.title }}</span>
							</el-breadcrumb-item>
						</transition-group>
					</el-breadcrumb>
				</div>

				<!-- <el-table :data="tableData">
					<el-table-column prop="date" label="" width="140">
					</el-table-column>
					<el-table-column prop="name" label="" width="120">
					</el-table-column>
					<el-table-column prop="address" label="">
					</el-table-column>
				</el-table> -->
				<router-view>
					<router-view></router-view>
				</router-view>
			</el-main>
		</el-container>
	</el-container>

</template>

<script>
	import {
		loginOut
	} from '../api/getData.js';
	import {
		setStorage,
		getStorage
	} from "../utils/localStorage.js";
	export default {
		data() {
			const item = {
				date: '2016-05-02',
				name: '',
				address: ' 1518 '
			};
			return {
				levelList: null, //
				userAccount: "", //
				userName:"",
				authorityName: "", //
				menuList: [], //
				tableData: Array(20).fill(item) //
			}
		},
		watch: {
			$route() {
				this.getBreadcrumb()
			}
		},
		created() {
			this.getBreadcrumb()
		},
		methods: {
			getBreadcrumb() {
				let matched = this.$route.matched.filter(item => item.meta && item.meta.title)
				const first = matched[0]
				if (!this.isIndex(first)) {
					matched = [].concat(matched)
					this.levelList = matched
				} else {
					this.levelList = [{
						path: '/',
						meta: {
							title: ''
						}
					}]
				}
			},
			isIndex(route) {
				const redirect = route && route.redirect
				if (!redirect) {
					return false
				}
				return redirect === '/'
			},
			handleSelect(index, indexPath) {
				console.log(index)
				console.log(indexPath)
			},
			//
			loginOutUser() {
				loginOut().then(()=>{
					this.$router.push({name: "login"})
				});
				this.$message.warning("");
			},
			//
			initRouter() {
				let userInfo = JSON.parse(getStorage("userInfo"));
				this.userName = userInfo.userName; //
				this.authorityName = userInfo.authorityName; //
			}
		},
		mounted() {
			this.$nextTick(() => {
				//
				this.activeIndex = this.$route.path;
				// console.log(this.$router.path+"path");
				this.initRouter();//
			})
		},

	}
</script>

<style>
	.menu-text {
		color: white !important;
	}

	.menu-float {
		float: right !important;
	}

	.menu-text:hover {
		background-color: rgb(67, 74, 80) !important;
	}

	.menu-text:focus {
		background-color: rgb(84, 92, 100) !important;
	}

	.menu-img {
		padding: 5px !important;
		border-radius: 15px;
		float: right !important;
	}

	.el-header {
		background-color: #B3C0D1;
		color: #333;
		line-height: 60px;
	}

	.el-aside {
		color: #333;
	}
</style>
