import request from '../utils/request.js'

	
export function selectAll(){
	return request({
		url: '/buildinfo/selectAll',//
		method: 'post',//
	})
}
export function getBuildAndAddress(){
	return request({
		url: '/buildinfo/getBuildAndAddress',//
		method: 'post',//
	})
}

export function getBuildByName(data){
	return request({
		url: '/buildinfo/getBuildByName',//
		method: 'post',//
		data
	})
}
export function queryByCondition(data){
	return request({
		url: '/addressinfo/queryByCondition',//
		method: 'post',//
		data
	})
}
export function edit(data){
	return request({
		url: '/buildinfo/edit',//
		method: 'post',//
		data
	})
}
export function addByName(data){
	return request({
		url: '/buildinfo/add',//
		method: 'post',//
		data
	})
}
export function deleteById(data){
	return request({
		url: '/buildinfo/deleteById',//
		method: 'post',//
		data
	})
}