<template>
  <div>
    <el-input
        style="width: 190px;margin-right: 40px;margin-top: 20px;margin-bottom: 20px;"
        v-model="listQuery.machineName"
        size="small"
        placeholder=""
        clearable
        class="filter-item"
    />
    <el-button-group style="margin-right: 20px;margin-top: 20px;margin-bottom: 20px;"
                     class="filter-item">
      <el-button
          :loading="searchloading"
          size="small"
          type="primary"
          icon="el-icon-search"
          @click="search"
      >
        
      </el-button>
      <el-button
          size="small"
          type="primary"
          icon="el-icon-refresh"
          @click="refresh"
      >
        
      </el-button>
      <el-button
          size="small"
          type="primary"
          icon="el-icon-plus"
          @click="insert"
      >
        
      </el-button>
    </el-button-group>

    <el-table
        v-loading="listLoading"
        :data="machineList"
        element-loading-text=""
        border
        fit
        height="500px"
        class="table-container"
        highlight-current-row
    >
      <el-table-column

          label=""
          width="100"
          align="center"
      >
        <template slot-scope="scope">
          {{ scope.row.machineId }}
        </template>
      </el-table-column>
      <el-table-column

          label=""
          width="200"
          align="center"
      >
        <template slot-scope="scope">
          {{ scope.row.machineName }}
        </template>
      </el-table-column>
      <el-table-column
          label=""
          width="180	"
          align="center"
      >
        <template slot-scope="scope">
          {{ scope.row.machineType }}
        </template>
      </el-table-column>
      <el-table-column
          label="(:)"
          width="170"
          align="center"
      >
        <template slot-scope="scope">
          <span>{{ scope.row.machineYear }}</span>
        </template>
      </el-table-column>
      <el-table-column
          label="(:)"
          width="200"
          align="center"
      >
        <template slot-scope="scope">
          {{ scope.row.machinePower }}
        </template>
      </el-table-column>

      <el-table-column
          fixed="right"
          label=""
          width="200"
          align="left"
      >
        <template slot-scope="scope">
          <el-button-group>
            <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="edit(scope)"
            >
              
            </el-button>
            <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="del(scope)"
            >
              
            </el-button>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
        style="margin-top: 10px;"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="listQuery.page"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="200"
        layout="total, sizes, prev, pager, next, jumper"
        :total="listQuery.total">
    </el-pagination>
    <el-dialog
        :visible.sync="dialogVisible"
        :title="dialogType === 'modify' ? '' : ''"
    >
      <el-form
          ref="dataForm"
          :model="temp"
          label-width="150px"
          label-position="right"
      >
        <el-form-item label="">
          <el-input v-model="temp.machineName" placeholder="" />
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="temp.machineType" placeholder="" />
        </el-form-item>
        <el-form-item label="(:)">
          <el-input v-model="temp.machineYear" placeholder="(:)" />
        </el-form-item>
        <el-form-item label="(:)">
          <el-input v-model="temp.machinePower" placeholder="(:)" />
        </el-form-item>

      </el-form>
      <el-button type="danger" @click="dialogVisible = false">
        
      </el-button>
      <el-button type="primary" @click="submit">
        
      </el-button>
    </el-dialog>
  </div>
</template>

<script>
import { queryByPage ,add ,update , deleteById} from '@/api/getMachineInfo.js';
import { deepClone } from '@/utils/index.js';
import { setStorage, getStorage} from "@/utils/localStorage.js";
import {Message} from "element-ui";
const _temp = {
  machineId: '',
  machineName: '',
  machineType: '',
  machineYear: '',
  machinePower: '',
}
export default {
  data() {
    return {
      searchloading:false, //
      listLoading:true,//
      machineList:[],       //
      listQuery:{       // 
        limit: 10,//
        page: 1,//
        total: 0,//
        machineName: "" //
      },
      temp: Object.assign({}, _temp),
      dialogVisible: false,   //
      dialogType: 'create',
    }
  },

  methods: {

    //
    handleSizeChange(value){
      this.listQuery.limit = value;
      //,
      this.initCarList();
    },
    //
    handleCurrentChange(value){
      this.listQuery.page = value;
      //,
      this.initCarList();
    },
    //
    initCarList(){
      this.listLoading = true;
      let data = {
        machineName: this.listQuery.machineName,
        page: this.listQuery.page,
        limit: this.listQuery.limit
      }
      // console.log("data\n")
      // console.log(data)
      queryByPage(data).then((res)=>{
        if(res != -1){
          //
          if(res.datas.length===0){  //
            this.listLoading = false;
            Message({
              message: "",
              type: "error"
            })
          }
          res.datas.forEach((item, index) => {
            //index
            item.index= (this.listQuery.page-1) * this.listQuery.limit+index+1;
            //
            //console.log(res.total);

            this.machineList = res.datas;
            this.listQuery.total = res.total;
            setTimeout(() => {
              this.listLoading = false;
            }, 200)
          })

        }

      })
    },
    //
    search(){
      this.searchloading = true
      this.initCarList()
      setTimeout(() => {
        this.searchloading = false;
      }, 1500)
    },
    //
    refresh() {
      this.listQuery.page=1;
      this.initCarList()
    },
    resetTemp() {
      this.temp = Object.assign({}, _temp)
    },
    insert() {
      this.resetTemp()
      this.dialogVisible = true
      this.dialogType = 'create'
      this.$nextTick(() => {
        this.$refs['dataForm'].clearValidate()
      })
    },
    edit(scope) {
      this.resetTemp()
      this.dialogVisible = true
      this.dialogType = 'modify'
      // console.log("this item is !!:\n")
      // console.log(scope.row)
      this.temp = deepClone(scope.row)
      this.$nextTick(() => {
        this.$refs['dataForm'].clearValidate()
        //TODO
      })
    },
    del(scope) {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        deleteById({id:scope.row.machineId}).then(()=>{
          this.initCarList()
          setTimeout(() => {
            this.list.splice(scope.$index, 1)  //
            this.$message({
              message: '',
              type: 'success'
            })
          }, 300)
        })

      })
    },
    submit() {
      if (this.listLoading) {
        return
      }
      this.listLoading = true
      //
      if(this.dialogType==="create"){
        // console.log("")
        // console.log( this.temp)
        add(this.temp)
        this.initCarList()
      }else  if(this.dialogType==="modify"){
        // console.log("")
        // console.log( this.temp)
        update(this.temp);
        this.initCarList()
      }
      setTimeout(() => {
        this.$message({
          message: '',
          type: 'success'
        })
        this.dialogVisible = false
        this.listLoading = false
      }, 100)
    }
  },

  mounted() {
    this.$nextTick(() => {
      this.initCarList();
    })
  },

}
</script>

<style>
.w-200{
  width: 200px;
}
.filter-item{
  display: inline-flex;
  vertical-align: middle;
  margin-bottom: 10px;
}
</style>