<template>
	<div>
		<el-select v-model="selectValue" placeholder="" size="small"
			style="margin-right: 40px;margin-top: 20px;margin-bottom: 20px;" @change="search()">
			<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
			</el-option>
		</el-select>
		<!-- <el-input style="width: 190px;margin-right: 40px;margin-top: 20px;margin-bottom: 20px;" v-model="selectValue"
			size="small" placeholder="" clearable class="filter-item" /> -->
		<el-button-group style="margin-right: 20px;margin-top: 20px;margin-bottom: 20px;" class="filter-item">
			<!-- <el-button size="small" type="primary" icon="el-icon-search" @click="search()">
				
			</el-button> -->
			<el-button size="small" type="primary" icon="el-icon-refresh" @click="refresh()">
				
			</el-button>
			<el-button size="small" type="primary" icon="el-icon-plus" @click="add()">
				
			</el-button>
			<el-button size="small" type="primary" icon="el-icon-plus" @click="sale()">
				
			</el-button>
		</el-button-group>
		<el-tag type="success">tips:10</el-tag>
		<el-table v-loading="listLoading" :data="carChangeList.slice((cur_page-1)*pageSize,cur_page*pageSize)"
			element-loading-text="" border fit height="500px" class="table-container" highlight-current-row>
			<el-table-column label="" width="100" align="center">
				<template slot-scope="scope">
					{{ scope.row.index }}
				</template>
			</el-table-column>
			<el-table-column label="" width="200" align="center">
				<template slot-scope="scope">
					{{ scope.row.carId }}
				</template>
			</el-table-column>
			<el-table-column label="" width="180	" align="center">
				<template slot-scope="scope">
					{{ scope.row.carChangeType }}
				</template>
			</el-table-column>
			<el-table-column label="" width="170" align="center">
				<template slot-scope="scope">
					<span>{{  parseInt(scope.row.operation) === 1 ? '' : '' }}</span>
				</template>
			</el-table-column>
			<el-table-column label="" width="170" align="center">
				<template slot-scope="scope">
					<span>{{ scope.row.carChangeId }}</span>
				</template>
			</el-table-column>
			<el-table-column fixed="right" label="" width="200" align="left">
				<template slot-scope="scope">
					<el-button-group>
						<!-- <el-button type="primary" icon="el-icon-edit" size="mini" @click="edit(scope)">
							
						</el-button> -->
						<el-button type="danger" icon="el-icon-delete" size="mini" @click="del(scope)">
							
						</el-button>
					</el-button-group>
				</template>
			</el-table-column>

		</el-table>
		<!-- ui -->
		<div style="margin-top:20px" class="pagination">
			<el-pagination background @current-change="handleCurrentChange" @size-change="handleSizeChange"
				:current-page="cur_page" :page-sizes="[10,15,20,50]" :page-size="pageSize"
				layout="total, sizes, prev, pager, next, jumper" :total="total"></el-pagination>
		</div>
		<el-dialog :visible.sync="dialogVisible" title="">
			<el-form ref="dataForm" :model="temp" label-width="150px" label-position="right">
				<!-- <el-form-item label="">
					<el-input v-model="temp.carChangeId" placeholder="" :disabled="true" />
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="temp.carId" placeholder="" />
				</el-form-item> -->
				<el-form-item label="">
				  <el-input v-model="temp.carNumber" placeholder="" />
				</el-form-item>
				<el-form-item label="">
				  <el-input v-model="temp.carStyle" placeholder="" />
				</el-form-item>
				<el-form-item label="(:)">
				  <el-input v-model="temp.carLoad" placeholder="(:)" />
				</el-form-item>
				<el-form-item label="(:)">
				  <el-input v-model="temp.carMileage" placeholder="(:)" />
				</el-form-item>
				<el-form-item label="">
				  <el-input v-model="temp.carBrand" placeholder="" />
				</el-form-item>
				<!-- <el-form-item label="">
					<el-input v-model="temp.carChangeType" placeholder="" />
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="temp.operation" placeholder="" />
				</el-form-item> -->
			</el-form>
			<el-button type="danger" @click="dialogVisible = false">
				
			</el-button>
			<el-button type="primary" @click="submit()">
				
			</el-button>
		</el-dialog>
		<el-dialog :visible.sync="saleDialogVisible" title="">
			<el-form ref="dataForm" :model="temp" label-width="150px" label-position="right">
				<el-form-item label="">
					<el-input v-model="temp.carChangeId" placeholder="" :disabled="true" />
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="temp.carId" placeholder="" />
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="temp.carChangeType" placeholder="" />
				</el-form-item>				
			</el-form>
			<el-button type="danger" @click="dialogVisible = false">
				
			</el-button>
			<el-button type="primary" @click="submit()">
				
			</el-button>
		</el-dialog>
	</div>
</template>

<script>
	import {
		setStorage,
		getStorage
	} from "@/utils/localStorage.js";
	import {
		deepClone
	} from "@/utils/index.js";
	import {
		getByOperation,
		insert,
		update,
		deleteCarChange,
		saleCar
	} from '@/api/getCarChange.js';
	const _temp = {
		carChangeId: '', //
		carId: '', //
		carChangeType: '', //
		operation: '' ,//or
		carNumber: '',
		carStyle: '',
		carLoad: '',
		carMileage: '',
		carBrand: '',
	}
	export default {
		data() {
			return {
				//
				saleDialogVisible:false,
				//
				selectValue: "",
				options: [{
					value: '1',
					label: ''
				}, {
					value: '0',
					label: ''
				}],
				listLoading: true, //
				inputData: "",
				carChangeList: [],
				temp: Object.assign({}, _temp),
				dialogVisible: false, //
				dialogType: '',
				cur_page: 1,
				pageSize: 10,
				//
				total: 0,
			}
		},

		methods: {
			init() {
				this.listLoading = true;
				getByOperation({}).then((res) => {
					if (res != -1) {
						res.datas.forEach((item, index) => {
							item.index = index + 1;
						})
						this.carChangeList = res.datas;
						this.total = this.carChangeList.length;
						this.listLoading = false;
					}

				})
			},
			refresh() {
				this.selectValue = "";
				this.init()
			},
			resetTemp() {
				this.temp = Object.assign({}, _temp)
			},
			add() {
				this.resetTemp()
				this.dialogVisible = true
				this.dialogType = 'buy'
				this.$nextTick(() => {
					this.$refs['dataForm'].clearValidate()
				})
			},
			edit(scope) {
				this.resetTemp()
				this.dialogVisible = true
				this.dialogType = 'modify'
				this.temp = deepClone(scope.row)
				//console.log(this.temp)
				this.$nextTick(() => {
					this.$refs['dataForm'].clearValidate()
				})
			},
			sale(){
				this.resetTemp()
				this.saleDialogVisible=true//
				this.dialogType='sale'
				this.$nextTick(() => {
					this.$refs['dataForm'].clearValidate()
				})
			},
			del(scope) {
				this.$confirm('', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					this.temp = deepClone(scope.row);
					let deldata = this.temp;
					deleteCarChange(deldata).then((res) => {
						if (res != -1) {
							this.$message({
								message: '',
								type: 'success'
							})
							this.init()
						}
						
					})
				})
			},
			submit() {
				if (this.listLoading) {
					return
				}
				let data=this.temp;
				if (this.dialogType == 'sale') {
					data.operation=0;
					saleCar(data).then((res) => {
						if (res != -1) {
							this.$message({
								message: '',
								type: 'success'
							})
							this.selectValue = "";
							this.init()
						}
						this.saleDialogVisible = false
					})
				} else {
					
					insert(data).then((res) => {
						if (res != -1) {
							this.$message({
								message: '',
								type: 'success'
							})
							this.selectValue = "";
							this.init()
						}
						this.dialogVisible = false
					})
				}
			},
			search() {
				let carChange = {
					operation: this.selectValue
				}
				this.listLoading = true;
				getByOperation(carChange).then((res) => {
					if (res != -1) {
						// console.log(carChange);
						res.datas.forEach((item, index) => {
							item.index = index + 1;
							//console.log(item)
						})
						this.carChangeList = res.datas;
						this.total=this.carChangeList.length;
						this.cur_page=1;
						this.listLoading = false;
					}
				})
			},
			// method
			handleSizeChange(val) {
				this.pageSize = val;
				this.cur_page = 1;
				// console.log(this.carChangeList.slice((this.cur_page-1)*this.pageSize,this.cur_page*this.pageSize));
			},
			// 
			handleCurrentChange(val) {
				this.cur_page = val;
			}
		},

		mounted() {
			this.$nextTick(() => {
				this.init();

			})
		},

	}
</script>

<style>
	.w-200 {
		width: 200px;
	}

	.filter-item {
		display: inline-flex;
		vertical-align: middle;
		margin-bottom: 10px;
	}
</style>
