import request from '../utils/request.js'

//carinfo
export function queryByPage(data){
	return request({
		url: '/carinfo/queryByPage',//
		method: 'post',//
		data
	})
}
//
export function add(data){
	return request({
		url: '/carinfo/add',//
		method: 'post',//
		data
	})
}
//
export function update(data){
	return request({
		url: '/carinfo/update',//
		method: 'post',//
		data
	})
}
export function deleteById(data){
	return request({
		url: '/carinfo/deleteById',//
		method: 'post',//
		data
	})
}