import request from '../utils/request.js'

//adInfo
export function queryByPage(data){
    return request({
        url: '/ad/queryAll',//
        method: 'post',//
        data
    })
}
//
export function add(data){
    return request({
        url: '/ad/add',//
        method: 'post',//
        data
    })
}
//
export function update(data){
    return request({
        url: '/ad/update',//
        method: 'post',//
        data
    })
}
export function deleteById(data){
    return request({
        url: '/ad/deleteById',//
        method: 'post',//
        data
    })
}