import request from '../utils/request.js'

	
export function getByOperation(data){
	return request({
		url: '/carchange/getByOperation',//
		method: 'post',//
		data
	})
}
export function update(data){
	return request({
		url: '/carchange/updateCarChange',//
		method: 'post',//
		data
	})
}
export function insert(data){
	return request({
		url: '/carchange/insertCarChange',//
		method: 'post',//
		data
	})
}
export function deleteCarChange(data){
	return request({
		url: '/carchange/deleteCarChange',//
		method: 'post',//
		data
	})
}
export function saleCar(data){
	return request({
		url: '/carchange/saleCar',//
		method: 'post',//
		data
	})
}