import request from '../utils/request.js'

	
export function userLogin(data){
	return request({
		url: '/login',//
		method: 'post',//
		data
	})
}

export function loginOut(data){
	return request({
		url: '/loginOut',//
		method: 'get',//
		data
	})
}


