<template>
    <div>
        <div style="margin-top: 15px;">
            <el-input placeholder="" v-model="queryData" class="input-with-select">
                <el-select v-model="selectData" slot="prepend" placeholder="" >
                    <el-option v-for="(item, index) in categoryList" :key="index" :label="item.categoryName" :value="item.categoryId"></el-option>
                </el-select>
                <el-button slot="append" icon="el-icon-search" @click="selectDoc"> </el-button>
            </el-input>
            <el-table
					v-loading="loading"
					element-loading-text=""
					element-loading-spinner="el-icon-loading"
					element-loading-background="rgba(0, 0, 0, 0.8)"
                    :data="docList"
                    stripe
                    style="width: 100%">
                <el-table-column
						width="100px;"
						align="center"
                        prop="docId"
                        label="ID">
                </el-table-column>
                <el-table-column
						width="150px;"
						align="center"
                        prop="categoryName"
                        label="">
                </el-table-column>
                <el-table-column
						align="center"
                        prop="docTitle"
                        label="">
                </el-table-column>
				<el-table-column
						align="center"
						show-overflow-tooltip
				        prop="docSummary"
				        label="">
				</el-table-column>
				<el-table-column
						align="center"
				        prop="docUploaduser"
				        label="">
				</el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
	import { getDocByCondition,getCategoryByCondition,docSelectOne } from '@/api/getData.js';
	import { setStorage, getStorage} from "@/utils/localStorage.js";
	export default {
		data() {
			return {
				docList: [],//doc
				selectData: "",//
				queryData: "",//
				categoryList: [],//
				loading:true,//
			}
		},
		methods: {
			//
			initCategoryList(){
				getCategoryByCondition({}).then(res => {
					//,
					if(res != -1){
						res.datas.unshift({categoryId : "", categoryName: ""});
						this.categoryList = res.datas;
					}
				});
			},
			//
			initDocList(){
				this.loading = true;
				///
				let data = {
					categoryId : this.selectData,
					docTitle: this.queryData
				}
				getDocByCondition(data).then((res) => {
					if(res != -1){
						this.docList = res.datas;
						this.loading = false;
					}
					//
					/* let filterArr = this.docList.filter((item, index) => {
						return item.docId % 5 == 0;
					}); */
				})
			},
			selectDoc(){
				docSelectOne({id: 105}).then()
			},
		},
		mounted() {			
			this.$nextTick(() => {
				//
				this.initDocList();
				//this.testMap();
				//
				this.initCategoryList();
			})
		},
		
	}
</script>

<style>

.table-style-thead{
	width:100%;	
}
.table-style-thead-th{
	width: 250px;
}
  .el-select .el-input {
    width: 130px;
  }
  .input-with-select .el-input-group__prepend {
    background-color: #fff;
  }
</style>